.acontent {
  position: relative;
  width: 90%;
  max-width: 400px;
  margin: auto;
  overflow: hidden;
}

.acontent .acontent-overlay {
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  height: 98%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  transition: all 0.4s ease-in-out 0s;
}

.acontent:hover .acontent-overlay {
  opacity: 1;
}

.acontent-image {
  width: 100% !important;
}

.acontent-details {
  position: absolute;
  text-align: center;
  padding-left: 1em;
  padding-right: 1em;
  width: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease-in-out 0s;
}

.acontent:hover .acontent-details {
  top: 50%;
  left: 50%;
  opacity: 1;
}

.acontent-details h3 {
  color: #fff;
  font-weight: 500;
  letter-spacing: 0.15em;
  margin-bottom: 0.5em;
  text-transform: uppercase;
}

.acontent-details p {
  color: #fff;
  font-size: 0.8em;
}

